










import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({
    components: {}
})
export default class MetadataEditorV extends Vue {
    @Prop() message!: string;
    @Prop() name!: string;

    $modals: any;

    onOk(): void {
        this.$emit('Ok');
        this.$modals.hide(this.name);
    }

    onCancel(): void {
        this.$emit('Cancel');
        this.$modals.hide(this.name);
    }
}
